<template>
	<div id="staffTable">
		<div class="table_title">详情列表</div>
		<!-- 表格搜索 -->
		<div class="search-content left clearfix">
			<span class="search_title">职位</span>
			<el-select allow-create v-model="dutyName" placeholder="全部">
				<el-option label="全部" value="全部">
				</el-option>
				<el-option v-for="item in  dutyArr " :label="item" :value="item" :key="item">
				</el-option>
			</el-select>
			<span class="search_title">姓名</span>
			<el-input v-model="searchName"></el-input>
			<span class="search_title">电话号码</span>
			<el-input v-model="searchTel"></el-input>
			<el-button @click="searchMethod">搜索</el-button>
		</div>

		<!--表格列表-->
		<div class="table-list">
			<el-table border style="width: 100%" :height="tableHeight" :data="staffList" stripe>
				<!--<el-table-column type="selection" min-width="35">
				</el-table-column>-->
				<el-table-column prop="employee_id" label="员工编号" align="center" min-width="15%">
				</el-table-column>
				<el-table-column prop="real_name" label="姓名" align="center" min-width="11%">
				</el-table-column>
				<el-table-column prop="phone" label="手机号码" align="center" min-width="15%">
				</el-table-column>
				<el-table-column prop="position_name" label="职位" align="center" min-width="11%">
				</el-table-column>
				<el-table-column prop="department_name" label="部门" align="center" min-width="11%">
				</el-table-column>
				
				<el-table-column label="微信绑定" align="center" min-width="11%">
					<template slot-scope="scope">
						<span size="small" v-if="scope.row.is_weixin==is_bind">绑定
						</span>
						<span size="small" v-else>未绑定
						</span>
					</template>
				</el-table-column>
				<el-table-column label="状态" align="center" min-width="11%">
					<template slot-scope="scope">
						<span size="small" v-if="scope.row.is_freeze==is_enable">启用
						</span>
						<span size="small" v-else>冻结
						</span>
					</template>
				</el-table-column>
				<el-table-column label="操作" min-width="15%" align="center">
					<template slot-scope="scope">
						<span size="small" type="info" @click="handleModifyStatus(scope.row, 1)" v-if="scope.row.is_freeze==is_enable" class="red">冻结
						</span>
						<span size="small" type="success" @click="handleModifyStatus(scope.row, 0)" v-else class="green">启用
						</span>
						<router-link :to="{path:'/staff/edit',query:{employeeId:scope.row.employee_id}}">
							<span size="small" type="text" class="blue">编辑
							</span>
						</router-link>
						<span size="small" type="text" @click="handleDelete(scope.row )" class="red">删除
						</span>
					</template>
				</el-table-column>
			</el-table>
			<div class="data-empty" v-if="isListEmpty">
				<div class="img">
					<img src="../../../assets/images/empty.png">
				</div>
				<div class="text">
					<span v-if="searchNone==false">暂无数据...</span>
					<span v-if="searchNone==true">暂无搜索数据...</span>
				</div>

			</div>
			<!--分页弹框-->
			<div class="pagination-container">
				<el-pagination small @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="showStaffList.page" :page-sizes="[10,20,30,40]" :page-size="showStaffList.limit" layout="sizes, prev, pager, next, jumper" :total="total">
				</el-pagination>
			</div>
		</div>
		<!--<div class="el-table__empty-block" v-if='showEmpty'>
			<div class="imgs">
				
			</div>
			<span>暂无数据...</span>
		</div>-->
		<!-- 删除提示弹窗 -->
		<delete-dialog v-if="DeleteDialogVisible" :isFreezed="isFreezed" @cancelClose="cancelClose" @confirmDelete="confirmDelete" @confirmIsfreeze="confirmIsfreeze" :isStaff="isStaff" :deleteValue="deleteValue"></delete-dialog>
	</div>
</template>

<script>
	import Vue from "vue";
	import { staffDataGet } from "../api";
	import { treeMethods } from "../index";
	import deleteDialog from "./children/children/deleteDialog"
	import { isFreeze, isWechat, isAdmin, isDelete, positionRight } from "@/config/cfg";
	export default {
		data() {
			return {
				isFreezed: false,
				freezeRow: "",
				freezeStatus: "",
				DeleteDialogVisible: false,
				isStaff: true,
				deleteValue:"",
				showEmpty: false,
				tableHeight: 0,
				dutyName: "", //下拉框中选中的部门名称
				searchName: "", //搜索名称或者编号
				searchTel: "", //搜索电话号码
				staffList: [],
				searchNone:false,
				deleteRow: {},
				searchList: [], //满足搜索的列表不随树的active而变化
				dutyArr: [],
				is_enable: "",
				is_bind: "",
				isListEmpty: false,
				showStaffList: {
					page: 1,
					limit: 10
				},
				total: 10
			};
		},
		components: {
			deleteDialog
		},
		created() {
			this.tableHeight = document.documentElement.clientHeight - (50 + 50 + 70 + 100);
			$(window).resize(() => {
				this.tableHeight = document.documentElement.clientHeight - (50 + 50 + 70 + 100);
			});
			this.initTableList();
			this.getAllDutyName();
		},

		computed: {
			departId() {
				return this.$store.state.staff.departId;
			},
			departInforList() {
				return this.$store.state.staff.departInforList;
			},
			pageSize() {
				return this.$store.state.staff.pageSize;
			},
			departNameSave() {
				return this.$store.state.staff.departNameSave;
			}
		},
		watch: {
			departId: function(val) {
				this.dutyName = "";
				this.searchName = "";
				this.saleTel = "";
				if(val == 0) {
					this.initTableList();
				} else {
					this.getDepartStaff();
				}
			}
		},
		methods: {
			//初始化列表
			initTableList() {
				staffDataGet.getStaffList({
					get_employee_list: 1
				}, (resp) => {
					this.staffList = resp.data.employee_list||[];
					this.searchList = resp.data.employee_list||[];
					this.is_enable = isFreeze.NO||"";
					this.is_bind = isWechat.NO||"";
					this.total = treeMethods.getTotal(this.searchList);
					this.staffList = treeMethods.getPageList(resp.data.employee_list, this.showStaffList.limit, this.showStaffList.page);
				if(this.staffList!==undefined&&this.staffList.length!== 0) {
					this.isListEmpty = false
					this.searchNone =false
				} else {
					this.isListEmpty = true
					this.searchNone =false
				}

				});
			},
			//点击树中的部门名称，列表显示对应的员工列表
			getDepartStaff() {
				if(this.departId !== 0) {
					let data = {
						get_department_employee: 1,
						department_id: this.departId
					};
					staffDataGet.getDepartStaffList(data, (resp) => {
						this.staffList = resp.data.employee_list|| [];
						this.isEmpty();
					});
				}
			},
			//显示暂无数据
			isEmpty() {
				if(this.staffList!==undefined&&this.staffList.length !== 0) {
					this.isListEmpty = false
				} else {
					this.isListEmpty = true
				}
			},
			//切换操作中的冻结启用
			handleModifyStatus(row, status) {
				this.isFreezed = true;
				if(status == 1) {
					this.DeleteDialogVisible = true;
				} else {
					let data = {
						freeze_employee: 1,
						employee_id: row.employee_id,
						is_freeze: status
					};
					staffDataGet.getUserInfor(data, (resp) => {
						if(resp.ret == 0) {
							row.is_freeze = !row.is_freeze;

						}
					})
				}
				this.freezeRow = row;
				this.freezeStatus = status;

			},
			//点击删除
			handleDelete(row) {
				this.deleteValue = row.real_name
				this.isFreezed = false;
				this.DeleteDialogVisible = true;
				this.deleteRow = row
			},
			//点击搜索
			searchMethod() {
				this.staffList = treeMethods.search(this.dutyName, this.searchName, this.searchTel, this.searchList);
				if(this.staffList!==undefined&&this.staffList.length !== 0) {
					this.isListEmpty = false
					this.searchNone =false
				} else {
					this.isListEmpty = true
					this.searchNone =true
				}
			},
			//选择每页显示的条目数
			handleSizeChange(val) {
				this.showStaffList.limit = val;
				this.staffList = treeMethods.getPageList(this.searchList, this.showStaffList.limit, this.showStaffList.page);
				this.total = treeMethods.getTotal(this.searchList);
				this.isEmpty();
			},
			//选择当前页码
			handleCurrentChange(val) {
				this.showStaffList.page = val;
				this.total = treeMethods.getTotal(this.searchList);
				this.staffList = treeMethods.getPageList(this.searchList, this.showStaffList.limit, this.showStaffList.page);
				this.isEmpty();
			},
			//获取职位列表
			getAllDutyName() {
				let data = {
					get_position_list: 1
				}
				staffDataGet.getDutyList(data, (resp) => {
					resp.data.position_list.forEach((item) => {
						if(this.dutyArr.indexOf(item.position_name) == -1) {
							this.dutyArr.push(item.position_name)
						}
					})
				})
			},
			confirmIsfreeze() {
				let data = {
					freeze_employee: 1,
					employee_id: this.freezeRow.employee_id,
					is_freeze: this.freezeStatus
				};
				staffDataGet.getUserInfor(data, (resp) => {
					if(resp.ret == 0) {
						this.freezeRow.is_freeze = !this.freezeRow.is_freeze;

					}
				})
				this.DeleteDialogVisible = false;

			},
			//点击确定删除
			confirmDelete() {
				this.DeleteDialogVisible = false;
				let data = {
					del_employee: 1,
					employee_id: this.deleteRow.employee_id,
				}
				staffDataGet.getUserInfor(data, (resp) => {
					if(resp.ret == 0) {
						this.initTableList()
					}
				})
			},
			//点击取消删除
			cancelClose() {
				this.DeleteDialogVisible = false;
			}

		}

	};
</script>

<style lang="scss" scoped>
	@import "src/styles/mixin.scss";
	#staffTable {
		background: #ffffff;
		box-shadow: 0 2px 4px 0 #becaeb;
	}
	
	.table_title {
		@include wh(100%, 40px);
		@include fc(14px, #666666);
		line-height: 40px;
		background-color: #f6f8fc;
		padding-left: 14px;
	}
	
	.search-content {
		min-width: 800px;
		margin: 20px 14px 20px 0;
	}
	
	.deleteBtn.el-button {}
	
	.search_title {
		font-size: 14px;
		padding: 14px;
	}
	
	.table-list {
		height: 100%;
		background: #FFFFFF;
		padding: 14px 14px 0 14px;
	}
	
	.imgs {}
	
	.pagination-container {
		margin: 50px 0 50px 30%;
		font-family: MicrosoftYaHei;
		font-size: 12px;
		color: #666666;
		letter-spacing: 0.43px;
	}
	
	.pageSize {
		font-size: 12px;
		width: 70px;
		height: 34px;
		margin-right: 10px;
	}
	
	div.el-select.el-input {
		font-size: 12px;
	}
	
	.data-empty {
		position: absolute;
		top: 35%;
		left: 53%;
	}
	
	.jump {
		font-size: 12px;
		margin-right: 10px;
	}
	
	.jump .el-input {
		font-size: 12px;
		width: 80px;
		height: 34px;
	}
	
	.jump .el-button {
		font-size: 12px;
		width: 30px;
		height: 34px;
		background: #F3F3F3;
	}
	
	.blue {
		font-size: 14px;
		color: #4877E7;
		margin-right: 14px;
		cursor: pointer;
	}
	
	.red {
		font-size: 14px;
		color: #E7487E;
		margin-right: 14px;
		cursor: pointer;
	}
	
	.green {
		font-size: 14px;
		color: limegreen;
		margin-right: 14px;
		cursor: pointer;
	}
	
	.data-empty {
		position: absolute;
		top: 50%;
		left: 60%;
		transform: translate(-50%, -50%);
		background: #fff;
		.img {
			@include wh(200px, 200px);
			img {
				@include wh(200px, 200px);
			}
		}
		.text {
			@include fc(14px, #9b9b9b);
			width: 200px;
			text-align: center;
			margin-top: 20px;
		}
	}
</style>
<style lang="scss">
	@import "src/styles/mixin.scss";
	#staffTable {
		font-size: 0;
		.search-content {
			.el-input {
				font-size: 14px;
				width: inherit;
				margin-right: 20px;
				.el-input__inner {
					@include wh(180px, 34px);
					border-radius: 0;
				}
			}
		}
		.el-button {
			@include wh(100px, 34px);
			border: 1px solid #4877e7;
			color: #4877e7;
			border-radius: 0;
			font-size: 14px;
			cursor: pointer;
		}
		.el-table {
			.el-button {
				@include wh(50px, 30px);
				color: #FFFFFF;
				border: none
			}
		}
		.el-table thead .cell {
			font-weight: 900;
		}
		.el-table__empty-text {
			display: none;
		}
	}
</style>


