<template>
	<div id="staffTree">
		<!--部门树-->
		<div class="tree_title">部门列表</div>
		<z-tree ref="tree" :treeData="treeData" @node-click="selectNodeActive" @delete-category="deleteCategorys" @editor-category="openEditorDialog" @add-category="addCategorys"></z-tree>

		<!--添加提示弹框-->
		<el-dialog class="category-dialog" :visible.sync="addDialogVisible" :show-close="false" size="" :before-close="handleClose">
			<div class="dialog-title" slot="title">添加部门</div>
			<div class="dialog-content">
				<div class="category clearfix">
					<div class="content-left left">
						<span>分类名称</span>
					</div>
					<div class="content-right left">
						<el-form :model="form" :rules="rules" ref="form">
							<el-form-item  prop="dialongInputValue">
						<el-input class="left" v-model="form.dialongInputValue" placeholder="请输入内容"></el-input>
						</el-form-item>
						</el-form>
					</div>
				</div>

				<div class="button-group clearfix">
					<div class="ok-btn left" @click="addConfirm('form')">确定</div>
					<div class="cancel-btn left" @click="addCancel">取消</div>
				</div>
			</div>
		</el-dialog>

		<!-- 删除提示弹窗 -->
		<el-dialog class="delete-dialog" :visible.sync="DeleteDialogVisible" :show-close="false" size="" :before-close="handleClose">
			<div class="dialog-content">
				<div class="img"></div>
				<span class="question" v-if="success_delete==false"></span>
				<span class="success-delete" v-if="success_delete==true"></span>
				<div class="delete-tip">
					<div class="delete-text">是否确定删除此部门？</div>
				</div>
				<div class="button-group clearfix">
					<div class="ok-btn left" @click="deleteConfirm">确定</div>
					<div class="cancel-btn left" @click="deleteCancel">取消</div>
				</div>
			</div>
		</el-dialog>
		<!--编辑提示弹框-->
		<el-dialog class="category-dialog" :visible.sync="EditorDialogVisible" :show-close="false" size="" :before-close="handleClose">
			<div class="dialog-title" slot="title">编辑</div>
			<div class="dialog-content">
				<div class="category clearfix">
					<div class="content-left left">
						<span>修改名称</span>
					</div>
					<div class="content-right left">
						<el-form :model="form" :rules="rules" ref="form">
							<el-form-item prop="dialongInputValue"> 
						<el-input class="left" v-model="form.dialongInputValue" placeholder="请输入内容"></el-input>
						</el-form-item>
						</el-form>
						
					</div>
				</div>
				<div class="button-group clearfix">
					<div class="ok-btn left" @click="editConfirm('form')">保存</div>
					<div class="cancel-btn left" @click="editCancel">取消</div>
				</div>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	import Vue from "vue";
	import ZTree from "../components/tree/tree.vue";
	import { staffDataGet } from "../api";
	import { treeMethods } from "../index";
	export default {
		data() {
			 var validateInput = (rule, val, callback) => {
		      if (!val) {
		        callback(new Error("部门名称不能为空"));
		      }else if(this.form.dialongInputValue.length>15){
		      	callback(new Error("长度在 1 到 30 个字符"));
		      }else if(this.departNameArr.indexOf(this.form.dialongInputValue) !== -1) {
		      	callback(new Error("该部门已经存在"));
		      } else {
			        callback();
			     }
		    };
			return {
				success_delete:false,
				addDialogVisible: false,
				EditorDialogVisible: false,
				DeleteDialogVisible: false,
				isEditor: false, //是编辑弹框
				dialogTitle: "",
				form:{
				dialongInputValue: ""	
				},
				treeData: [],
				treeDataById: null,
				departNameArr: [],
				name: null,
				rules:{
				dialongInputValue:[{validator:validateInput, trigger: 'click' }]
				}
			}
		},
		created() {
			this.getCategory()
		},
		computed: {
			departId() {
				return this.$store.state.staff.departId;
			}
		},
		components: {
			ZTree
		},
		watch: {
			treeData: {
				handler: function(val) {
					this.treeDataByIds();
				},
				deep: true
			}
		},
		methods: {
			//拉取分类
			getCategory() {
				staffDataGet.getDepartList({
					get_department_list: 1
				}, (resp) => {
					let data = resp.data.department_list;
					let staffObj = treeMethods.addTitleToTree(data);
					Vue.set(this.treeData, 0, staffObj);
					treeMethods.generateKey(this.treeData, 0);
					treeMethods.initTreeData(this.treeData);
					treeMethods.selectIcon(this.treeData);
					this.departNameArr = treeMethods.saveDepartName(this.treeData);
					this.$store.commit("GET_NAME_ARR", this.departNameArr);
					 this.$emit("margin-change");
				})
			},
			//检索对应id
			treeDataByIds() {
				this.treeDataById = treeMethods.treeDataById(this.treeData);
			},
			//点击子节点
			selectNodeActive(node) {
				treeMethods.changeSelecte(node, this.treeDataById)
				this.$store.commit("GET_STAFF_LIST", node.employee_list);
				if(node.department_id !== undefined) {
					this.$store.commit("CHANGE_STAFF_ID", node.department_id);
				}
			},
			//点击添加
			addCategorys(node) {
				this.form.dialongInputValue = ""
				this.addDialogVisible = true;
				this.name = node
			},
			//点击删除
			deleteCategorys(node) {
				this.DeleteDialogVisible = true;
				this.name = node
			},
			//点击编辑
			openEditorDialog(node) {
				if(node.hasOwnProperty('employee_list')) {
					this.EditorDialogVisible = true
					this.name = node
						let data = {
							get_department_info: 1,
							department_id: this.name.department_id,
						}
						staffDataGet.getDepartList(data, (resp) => {
							this.form.dialongInputValue = resp.data.department_name
							this.$emit("margin-change");
						})
				}
			},
			//点击确定添加
			addConfirm(formName) {
				this.$refs[formName].validate(valid => {
					 if (valid) {
				let data = {
					department_save: 1,
					department_name: this.form.dialongInputValue,
				}
				staffDataGet.modifyDepartInfo(data, this.getCategory)
				this.addDialogVisible = false;
				 this.$emit("margin-change");
				}
					 })
			},
			//点击取消添加
			addCancel() {
				this.addDialogVisible = false;
			},
			//点击确定删除
			deleteConfirm() {
				if(!this.name.hasOwnProperty('employee_list')||(this.name.hasOwnProperty('employee_list') && this.name.employee_list.length == 0)) {
					this.DeleteDialogVisible = false;
					let data = {
						department_del: 1,
						department_id: this.name.department_id,
					}
					staffDataGet.modifyDepartInfo(data, this.getCategory)
				} else {
					this.$slnotify({
		            message: "该部门包含员工信息，不能被删除",
		          });
				}
			},
			//点击取消删除
			deleteCancel() {
				this.DeleteDialogVisible = false;
			},
			//点击编辑保存
			editConfirm() {
				if(this.form.dialongInputValue=="") {
					alert("部门名称不能为空")
				}else {
					if(this.departNameArr.indexOf(this.form.dialongInputValue) !== -1) {
					alert("不能修改为已经存在的部门名称")
				} else {
					this.getEditName()
					this.EditorDialogVisible = false;
					this.form.dialongInputValue = "";
				}

			}
				
			},
			//点击编辑取消
			editCancel() {
				this.EditorDialogVisible = false;
			},
			//获取修改名称数据
			getEditName() {
				let data = {
					department_save: 1,
					department_id: this.name.department_id,
					department_name: this.form.dialongInputValue,
				}
				staffDataGet.modifyDepartInfo(data, this.getCategory)
			},
			//弹窗关闭前回调
			handleClose() {
				this.EditorDialogVisible = false;
				this.isEditor = false;
				this.form.dialongInputValue = "";
				this.addDialogVisible = false;
				this.EditorDialogVisible = false;
				this.DeleteDialogVisible = false;
			}

		}

	};
</script>

<style lang="scss" scoped>
	@import "src/styles/mixin.scss";
	#staffTree {
		background: #ffffff;
		box-shadow: 0 2px 4px 0 #becaeb;
	}
	
	.tree_title {
		@include wh(100%, 40px);
		@include fc(14px, #666666);
		line-height: 40px;
		background-color: #f6f8fc;
		padding-left: 14px;
	}
	
	.category-dialog {
		top: 20%;
		.dialog-title {
			@include wh(540px, 40px);
			@include fc(16px, #fff);
			background-color: #5a8cff;
			text-align: center;
			line-height: 40px;
		}
	}
	
	.dialog-content {
		@include wh(540px, 200px);
	}
	.dialog-content .img{
		background: url(image/topbar.png);
		position: absolute;
		top: 0;
		width: 540px;
		height: 4px;
	}
	
	.question {
		position: absolute;
		display: inline-block;
		top: 20%;
		left: 15%;
		background: url(image/question_img_normal.png);
		width: 50px;
		height: 50px;
		z-index: 999999;
	}
	.success-delete{
		position: absolute;
		display: inline-block;
		top: 44%;
		left: 49%;
		background: url(image/success_img_normal.png);
		width: 50px;
		height: 50px;
	}
	.category {
		.content-left {
			@include wh(80px, 34px);
			line-height: 34px;
			margin-left: 60px;
		}
		.content-right {
			@include wh(320px, 34px);
		}
	}
	
	.delete-dialog {
		.dialog-title {
			@include wh(540px, 40px);
			@include fc(16px, #fff);
			text-align: center;
			background-color: #5a8cff;
			line-height: 40px;
		}
		.dialog-content {
			@include wh(540px, 200px);
			.delete-tip {
				margin-bottom: 60px;
				.delete-text {
					display: inline-block;
					padding-bottom: 15px;
					padding-left: 30%;
					vertical-align: bottom;
					@include fc(14px, #666);
				}
			}
		}
	}
	
	.button-group {
		margin-top: 40px;
		div {
			@include wh(160px, 40px);
			border-radius: 4px;
			border: 1px solid #5a8cff;
			color: #5a8cff;
			line-height: 40px;
			text-align: center;
			cursor: pointer;
		}
		.ok-btn {
			margin: 0 60px 0 77px;
			background-color: #5a8cff;
			color: #fff;
		}
	}
</style>

<style lang="scss">
	@import "src/styles/mixin.scss";
	#staffTree {
		.category {
			.el-input {
				width: 320px;
			}
			.el-input__inner {
				border-radius: 0;
				height: 34px;
			}
			.el-input__inner:hover {
				border: 1px solid #bfcbd9;
			}
			.el-input__inner:focus {
				border: 1px solid #bfcbd9;
			}
		}
		.el-dialog {
			top: 20%;
		}
		.el-dialog .el-dialog__header {
			padding: 0;
			margin-bottom: 60px;
		}
		.el-dialog__body {
			padding: 0;
		}
	}
</style>


